@import url(https://fonts.googleapis.com/css?family=Hammersmith+One);
@import url(https://fonts.googleapis.com/css?family=Monda|Hammersmith+One);
@import url(https://fonts.googleapis.com/css?family=Monda|Sintony);
@import url(https://fonts.googleapis.com/css?family=Monda|Sintony|Raleway|Hammersmith+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: Sintony;
  color: #888888;
}

h3 {
  margin: 0px;
}

a {
  font-family: Sintony;
  color: #ce893d;
}

strong {
    color: #ce893d;
    font-weight: bold;
    text-decoration: none;
}

.page-title {
  text-align: center;
  font-family: Hammersmith One;
  font-size: 6vw;
  color: #494e6b;
  padding: 10px;
  background-image: url(/static/media/header.7a03f8fd.png);
  background-position: 50% 110%;
  background-repeat: no-repeat;
  background-size: 70%;
  height: 12vw;
  margin-bottom: 10px
}

.content {
  margin: 0 10% 0 10%;
  background-color: #FFF;
}

.content-text {
  font-size: 80%;
  padding: 20px;
}

@media (max-width:750px) {
  .page-title {
      height: 14vw;
  }
}

@media (max-width:400px) {
  .page-title {
    background-position: 50% 100%;
      height: 16vw;
  }
}

@media (max-width:300px) {
  .page-title {
    background-position: 50% 100%;
    height: 19vw;
  }
}

.header {
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
  display:flex;
  align-items: center;
  justify-content: center;
  background: #363a4e;
}

.title {
  position: relative;
  display: flex;
  justify-content: flex-end;
  color: #fff;
  font-size: 4vw;
}
/* Word + SVG styles */
.word {
	position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 0 0.25em 0;
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    grid-area: title;
    font-size: 11vw;
    font-weight: 400;
    color: #fff;
    font-family: 'Hammersmith One', sans-serif;
}

.word .char2,
.word .char9 {
	color: #f7882f;
}

.title {
  color: #fff;
  font-family: 'Monda', sans-serif;
}

.title span, .word span {
	display: block;
	position: relative;
	flex: none;
	white-space: pre;
}



.shapes {
	position: absolute;
	top: 0;
	left: 0;
  width: 100vw;
  height: 100vh;
}

.section-title {
  margin: 0px;
  font-family: Monda;
  color: #494949;
  font-size: 2vw;
}

@media (max-width:600px) {
  .section-title {
    font-size: 4vw;
  }
}

.wrapper {
  margin-left: 10%;
  margin-right: 10%;
  height: 100%;
}

.project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.project img {
  max-width: 70vw;
}

.preview {
  justify-content: left;
}
.thumbnail {
  flex: 1 1 100%;
}

.right {
  flex: 1 1 100%;
}

.desc {
  margin-bottom: 10px;
  font-family: Sintony;
  color: #888888;
}

.project-title {
  margin: 0px;
  font-family: Monda;
  color: #494949;
}

.technologies {
  font-family: Sintony;
  color: #ce893d;
  border-top: 1px solid #cccccc;
  border-bottom: 1px solid #cccccc;
  padding: 3px 0px;
  margin-bottom: 6px;
}

.project > * {
  flex: 1 1 100%;
}

.contracted .preview div.desc {
  max-height: 100px;
  overflow-y: hidden;
}

.project-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.readMoreLess {
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #dfdfdf;
  font-family: Sintony;
  color: #ce893d;
  margin-bottom: 15px;
}


.readMoreLess span:hover {
  text-decoration: underline;
  cursor: pointer;
}

.icon {
  padding-right: 6px;
}

.fa-github-alt {
  padding-left: 8px;
}

.desc, .technologies, .readMoreLess {
  font-size: 2.7vw;
}

.project-title {
  font-size: 4.2vw;
}


/* Width is at least 600px */
@media screen and (min-width: 600px) {
  .preview {
    justify-content: center;
  }

  .thumbnail {
     flex: 1 1 27%;  /*grow | shrink | basis */
     padding-right: 20px;
     max-width: 180px;
  }

  .thumbnail img {
      display: block;
      width: 100%;
      max-width: 350px; /*actual image width*/
      height: auto; /* maintain aspect ratio*/
      margin: auto; /*optional centering of image*/
  }

  .right {
    flex: 1 1 73%;
  }
  .project .desc, .project .technologies, .project .readMoreLess {
    font-size: 2vw;
  }

  .project .project-title {
    font-size: 3vw;
  }
}

@media screen and (min-width: 700px) {
    .project .desc, .project .technologies, .project .readMoreLess {
      font-size: 1.5vw;
    }

    .project .project-title {
      font-size: 2.5vw;
    }
}

@media screen and (min-width: 850px) {
    .project .desc, .project .technologies, .project .readMoreLess {
      font-size: 1vw;
    }

    .project .project-title {
      font-size: 2vw;
    }
}

* {
  box-sizing: border-box;
}

.nav {
  height: auto;
  width: 100%;
  background-color: #494e6b;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fixed-nav {
  position: fixed;
  top: 0;
}

.brand {
  background-color: #f7882f;
  text-align: center;
}

.nav .brand a {
  color: #fff;
  font-family: Hammersmith One;
}

.brand a:hover {
  text-decoration: none !important;
}

.nav > .nav-btn {
  display: none;
}

.nav > div {
  display: flex;
  justify-content: space-around;
  flex: 0 1 15%
}

.nav a {
  font-family: Raleway;
  display: inline-block;
  padding: 10px 40px;
  text-decoration: none;
  color: #f7c331;
  font-size: 1.6vw;
}

.nav a:hover {
  text-decoration: underline;
}

.nav > #nav-check {
  display: none;
}

.nav > .nav-left {
  order: 1
}

.nav > .nav-right {
  order: 3
}

.nav > .nav-piece-center {
  order: 2;
}

@media (max-width:600px) {

  .nav.none > .nav-piece {
    display: none;
  }

  .nav.dropdown {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .nav.dropdown > .nav-piece {
    flex: 1 0 100%;
    flex-wrap: wrap;
  }

  .nav > .nav-piece-center {
    order: 0;
    text-align: center;
  }

  .nav > .nav-btn {
    order: 1;
    flex: 0 0 60%;
    display: flex;
    justify-content: flex-end;
  }

  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }

  .nav > .nav-btn > label:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }

  .nav  a {
    flex: 1 0 100%;
    font-size: 4vw;
  }
}

